body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.header{
  padding:20px 70px;
  background-color: black;
  color: white;
  display: flex;
  justify-content: space-between;
}
.cart-icon .counter{
  height: 20px;
  width: 20px;
  background-color: blue;
  border-radius: 50%;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  right: 7px;
  top: -6px;

}
.cart-icon{
  display: flex;
}
.icon {
  font-size: 22px;
}
img{
  height: 150px;
}
.card-wrapper{
  display: inline-flex;
  width: 320px;
  box-shadow:  0px 0px 7px black ;
  border-radius: 5px;
  gap:10px;

}
.image-wrapper{
  width: 180px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px;
  border-right: 1px solid grey;
}
.content{
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 10px;
}
.price{
  margin-bottom: auto;
}
.content button{
  background-color: black;
  color: white;
  border-radius: 3px;
}
.quantity{
  display: flex;
  align-self: flex-end;
  border-radius: 3px;
  border: 1px solid green;
}
.quantity div{
  height: 25px;
  width: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.quantity > div:first-child,
.quantity > button{
  background-color: rgb(0, 0, 255);
  color: white;
  border-radius: 0;
}
.quantity > button:disabled{
  opacity: 0.7;
}
main{
  padding: 20px;
  display: flex;
  flex-wrap: wrap;
  min-height:  80vh ;
  justify-content: center;
  align-items: center;
  align-content: center;
  gap: 50px;
}
.total{
  box-sizing: border-box;
  position: fixed;
  background-color: gray;
  padding: 17px 30px;
  padding-right: 30px;
  bottom: 0;
  width: 100%;
  text-align: end;

}